import * as tracking from '~global-js/helpers/tracking';

/**
 * This is a tracking function to record allocated amount of a/b test.
 * It may be imported both for modal and microapp.
 * @param {string} variant
 * @param {string} type 'Modal', 'Page'
 *
 */

export default function sendTrakOfABTest(variant, type) {
  tracking.event({
    mp: [{
      type: 'track',
      key: 'Set AB Testing',
      params: {
        'AB Testing Variant': variant,
        'AB Type': type,
      },
    }],
  });
}



